import Vue from 'vue'
import App from './App'

import Loading from '@/srcJs/loding.js'

import router from './router'
import store from './store'

import '@/assets/css/app.css'

Vue.use(Loading, {size: 100, indst:(name, value) =>  console.log(name, value)});

console.log(Loading)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
	el: '#app',
	router,
	store,
	components: { App },
	template: '<App/>'
})



