function loding(key, value) {
	if(value !== undefined) {
		localStorage.setItem(key, JSON.stringify(value));
	} else {
		return JSON.parse(localStorage.getItem(key)) || {}
	}
}

export default {
	install:function(vm, option) {
		vm.prototype.$local = loding;
	}
}


