<template>
    <div class="com_header">
        <p>{{headerList }}</p>
        <p>头部导航信息</p>
        <p>个人信息</p>
    </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
    created: function() {
        return
    },
    data: function(){
        return {
            setHeader: null,
        }
    },
    computed: {
        headerList: function() {
            var that = this;
            var str = this.$store.state.titleList.filter(function(inte) {
                if(inte == 'homePage') {
                    return false
                } else {
                    return inte
                };
            });
            str.unshift('homePage');
            str.forEach(function(inte, i) {
                str[i] = that.title[inte]
            });
            return str.join('/');
        },
        ...mapState([
            'title'
        ])
    }
}
</script>


