<template>
	<div class="register">
		<h3 class="register_title">登录</h3>
		<div class="register_input">
			<input ref="text" type="text" name="">
			<input type="password" name="">
			<div @click="sublite">一键登录</div>
			<div @click="sethomepage">返回首页</div>
		</div>
		
	</div>
</template>

 <script>
 export default {
 	methods: {
 		sublite: function() {
 			var name = this.$refs.text.value;
 			var path = '/' + this.$route.query.plan;
 			if(name){
 				this.$local("keys", {"loadin": true, "name": name})
 				this.$router.push(path)
 			} 
 				
 		},
 		sethomepage: function() {
 			this.$router.push('/')
 		}
 	}
 }
</script>



