<template>
   <li class="sidebar_lia">
        <router-link 
            :style="paddinLeft"
            :class="[tier === 0? 'sidbar_title': 'sidebar_secondary']"
            tag="div"
            v-if="setOff"
            :to="{path: setPath(OnlyChildren.path) }"
            active-class="is_active"
        >   
            <i v-if="tier === 0" class="ioc_1"></i>
            <span>{{title[OnlyChildren.meta.title]}}</span>
            <div v-if="!setOff" class="arrow"></div>
        </router-link>
        <div v-else >
            <div :style="paddinLeft"
                :class="[tier === 0? 'sidbar_title' : 'sidebar_secondary']" 
                @click.self="onUnfoid($event)">
                <i v-if="tier === 0" class="ioc_1"></i>
                <span>{{title[inte.meta.title]}}</span>
                <div v-if="!setOff" class="arrow"></div>
            </div> 
            <ul v-show="showOff" >
                <content-list 
                    v-for="child in inte.children" 
                    :inte="child" 
                    :tier="addTier"
                    :key="child.path"
                    :box-path="setPath(inte.path)"
                   
                ></content-list>
            </ul>
        </div>
   </li>
</template>

<script>
import { mapState } from 'vuex'

export default {
	name: 'contentList',
	props: {
        tier: {
            type: Number,
            default: 0
        },
        inte: {
            type: Object,
            default: {}
        },
        boxPath: {
            type: String,
            default: ''
        }
	},
    data: function() {
        return {
            OnlyChildren: null,
            setOff: null,
            showOff: false
        }
    },
    computed: {
        addTier: function() {
            var tier = this.tier;
            return  ++tier;
        },
        paddinLeft: function() {
            return {
                'padding-left': this.tier * 20 + 20+ 'px'  
            } 
        },
        ...mapState([
            'title'
        ])
    },
    created: function() {
        if(!this.inte.children) {
            this.OnlyChildren = this.inte;
            this.setOff = true;
        } else if(this.inte.children.length === 1) {
            this.OnlyChildren = this.inte.children[0]; 
             this.setOff = true;
        } else {
            this.setOff = false; 
        }
    },
    methods: {
        setPath: function(childrenPath) {
            if(this.boxPath == childrenPath) {
                return this.boxPath
            } else {
                var paths = this.boxPath + '/' + childrenPath;
                return  paths
            }
        },
        onUnfoid: function(vm) {
            this.showOff = !this.showOff;
        },
    },

}
</script>




