import Vue from 'vue'
import Router from 'vue-router'
import store from '@/store'

import Sidebar from '@/components/home-page'
import register from '@/components/register'

import items from '@/content/HomePage'
import workbench from '@/content/workbench'
import Document from '@/content/document'

import backToTop from '@/min-content/backToTop'

Vue.use(Router);
export var setrouterList = [
        {   
            path: '',
            component: Sidebar,
            redirect: 'homePage',
            children: [
                {
                    path: 'homePage',
                    component: items,
                    meta: {title: 'homePage'}
                },
            ]
        },
        {
            path: '/documents',
            component: Sidebar,
            redirect: '/documents/index',
            children: [
                {
                    path: 'index',
                    component: Document,
                    meta: {title: 'documents'}
                }
            ]
        },
        {
            path: '/introduction',
            component: Sidebar,
            redirect: '/introduction/index',
            children: [
                {
                    path: 'index',
                    component: Document,
                    meta: {title: 'introduction'}
                }
            ]
        },
        {
            path: '/nested',
            component: Sidebar,
            redirect: '/nested/munt1',
            meta: {title: 'nested'},
            children: [
                {
                    path: 'munt1',
                    component: Document,
                    meta: {title: 'munt1'}
                },
                {
                    path: 'munt2',
                    component: Document,
                    meta: {title: 'munt2'},
                    children: [
                        {
                            path: 'munt2-1',
                            component: Document,
                            meta: {title: 'munt2-1'}
                        },
                        {
                            path: 'munt2-2',
                            component: Document,
                            meta: {title: 'munt2-2'},
                            children: [
                                {
                                    path: 'munt2-2-1',
                                    component: Document,
                                    meta: {title: 'munt2-2-1'}
                                },
                                {
                                    path: 'munt2-2-2',
                                    component: Document,
                                    meta: {title: 'munt2-2-2'}
                                },
                                {
                                    path: 'munt2-2-3',
                                    component: Document,
                                    meta: {title: 'munt2-2-3'}
                                }
                            ]
                        },
                        {
                            path: 'munt2-3',
                            component: Document,
                            meta: {title: 'munt2-3'}
                        }
                    ]
                },
                {
                    path: 'munt3',
                    component: Document,
                    meta: {title: 'munt3'}
                },
                {
                    path: 'munt4',
                    component: Document,
                    meta: {title: 'munt4'}
                },
            ]
        },
        {
            path: '/backTOTop',
            component: Sidebar,
            redirect: '/backTOTop/index',
            children: [
                {
                    path: 'index',
                    component: backToTop,
                    meta: {title: 'backTOTop'}
                }
            ]
        },

    ]

var router = new Router({
    // mode: 'history',
    routes: setrouterList
})

router.beforeEach(function(to, from, next) {
    var a = to.path.split('/').filter(function(inte) {
        if(inte === '' || inte === 'index') {
            return false
        } else {
            return inte
        }
    });
    store.commit('setTitleList', {array: a});

    next()
});

export default router


