<template>
    <div class="container">
        <ul class="sidebar">
            <content-list 
                v-for="inte in setrouterList" 
                :inte="inte"  
                :tier="0"
                :key="inte.path"
                :box-path="inte.path"
                ></content-list>
        </ul>
        <div id="content">  
            <com-header></com-header>
            <com-nav></com-nav>
            <router-view></router-view>
        </div>
    </div>
</template>

<script>
import contentList from '@/components/sidebar.vue'
import comHeader from '@/components/comHeader.vue'
import comNav from '@/components/comNav.vue'
import { setrouterList } from '@/router'
export default {
    components: { contentList, comHeader, comNav },
	data: function(){
		return {
            setrouterList,
		}
	},
}
</script>




