<style type="text/css">
.backToTop {
    position: relative;
}

.backToTop div {
    height: 50px;
}

.bthTop {
    position: fixed;
    bottom: 40px;
    right: 40px;
    width: 80px;
    height: 40px;
    line-height: 40px;
    border: 1px solid red;
    text-align: center;
}
</style>

<script>
export default {
    data: function() {
        return {
            setshowTop: false,
            scrollTop: 0,
        }
    },
    mounted: function() {
        window.addEventListener('scroll', this.getScrollTop)
    },
    methods: {
        getScrollTop: function() {
            this.scrollTop = window.pageYOffset;
            if(this.scrollTop > 500) {
                this.setshowTop = true
            } else {
                this.setshowTop = false
            }
        },
        goTOp: function() {
            var scroll = this.scrollTop;
            var stopTime = setInterval(function() {
                scroll -= 40;
                window.scrollTo(0, scroll)
                if (scroll <= 0) {
                    scroll = 0;
                    clearInterval(stopTime)
                }
            },20)
        }
    }
}  
</script>



