<template>
	<div>	
		<div>工作台内容</div>
        <div>{{$store.state.count}}</div>
        <div @click="console_ll">12521555555</div>
        <div>{{num2}}</div>
        <div>{{str}}</div>
	</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'

export default {
    data() {
        return {
            a: 1
        }
    },
    computed: {
        add: function() {
            return this.a + 1
        },
        num2: function() {
            return this.$store.getters.num2(12)
        },
        ...mapState({
            count: 'count',
            num: 'num'
        }),
        ...mapGetters({
            str: 'str'
        })
    },
    methods: {
        console_ll() {
            console.log(this);
        },

    }
}
</script>




